import {
		mapState,
		mapMutations
    } from 'vuex';
import md5 from '../../kayida/common/md5.js'

export default {
	computed: {
	            ...mapState(['userInfo'])  
	        },
	data() {
		return {
			information:'',
			realname:'',
			mobile:'',
			code:'',
			bank_card:'',
			wx:'',
			time:0,
			times:''
		}
	},
	methods: {
		inforGetAuth(){
			var data = {
				mobile: this.mobile,
				send_type:'information_change'
			}
			this.request.httpRequest('api/sms/sendCode',data,'POST').then(res => {
				if (res.data.code === 1000){
					uni.showToast({
						title: res.data.msg,
						icon: "none"
					})
					this.time = 1;
					this.times = 59;
					setInterval(()=>{
						this.times --;
						if(this.times < 1){
							this.time = 2;
							clearInterval()
						}
					},1000)
				}
				if (res.data.code !== 1000){
					uni.showToast({
						title: res.data.msg,
						icon: "none"
					})
				}
			})
		},
		amend() {
			let data = {
				nickname:this.information.nickname,
				head_pic:this.information.head_pic,
				wx:this.wx,
				mobile:this.mobile,
				code:this.code,
				realname:this.realname,
				bank_card:this.bank_card,
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/user/updateProfile',data,'GET').then(res => {
				if(res.data.code === 1000){
					uni.showToast({
						title:"修改成功",
						icon:"none"
					})
					setTimeout(function(){
						uni.switchTab({
							url:"../user/user"
						})
					},1000)
				}else{
					uni.showToast({
						title:res.data.msg,
						icon:"none"
					})
				}
			})
		},
		in_logout() {
			this.logout()
			this.outbind()
		},
		...mapMutations(['logout','whetherbind','outbind']),
	},
	onLoad() {
		let data = {
			user_id: this.userInfo.user_id,
			token: this.userInfo.token,
			sign: md5(this.userInfo.user_id+this.userInfo.token)
		}
		this.request.httpRequest('api/user/index',data,'GET').then(res =>{
				this.information = res.data.retval
			}
		)
	},
}
